<template>
  <div>
    <login-form @submit="onSubmit"></login-form>
  </div>
</template>

<script>
import LoginForm from '@/components/account/SignInForm.vue'
import Vue from 'vue'
import cookies from 'vue-cookies'
import mainRequest from "@/api/mainRequest";


Vue.use(cookies)

export default {
  name: 'SignInPage',
  components: {
    LoginForm
  },
  data () {
    return {
      isLogin: false
    }
  },
  mounted () {
    if (this.$store.state.isAuthenticated != false) {
      this.isLogin = true
    } else {
      this.isLogin = false
    }
  },
  methods: {
    onSubmit (payload) {
      if (!this.isLogin) {
        const { email, password } = payload
        mainRequest
          .post('/member/sign-in', { email, password })
          .then((res) => {
            if (res.data) {
              alert(' !')
              this.$store.state.isAuthenticated = true
              this.$cookies.set('user', res.data, 3600)
              localStorage.setItem('userInfo', JSON.stringify(res.data))
              this.isLogin = true
              const token = JSON.parse(localStorage.getItem('userInfo'))
              mainRequest
                .post('/cart/validate', token)
                .then((res) => {
                  if (res.data) {
                    console.log('  .')
                    const [memberId, authorityName] = res.data.split(':')
                    if(authorityName === 'MANAGER') {
                      this.$store.state.isManager = true
                    }
                    localStorage.setItem('memberId', memberId)
                    localStorage.setItem('authorityName', authorityName)
                    const user = localStorage.getItem('memberId')
                  }
                })
              this.$router.push('/')
            } else {
              alert('     .')
            }
          })
          .catch((res) => {
            alert(res.response.data.message)
          })
      } else {
        alert('   !')
      }
    }
  }
}
</script>

<style scoped></style>
