<template>
  <div class="grey lighten-5" style="font-family: Arial">
    <v-row justify="center">
      <v-col cols="auto" style="padding-bottom: 90px">
        <router-link to="/">
          <v-img
            :src="require('@/assets/logo.png')"
            width="120"
            class="mx-auto mb-6"
          />
        </router-link>
        <v-card width="460">
          <v-card-text class="text-center px-12 py-16">
            <v-form @submit.prevent="onSubmit" ref="form">
              <div class="text-h4 font-weight-black mb-10"> </div>

              <v-divider
                class="mb-8"
                style="border-width: 1px; border-color: black"
              ></v-divider>
              <div align="right">
                <input
                  type="checkbox"
                  style="accent-color: green; margin-right: 6px"
                  name="authority"
                  value="true"
                  v-model="manager"
                />
                <label> </label>
              </div>
              <div v-if="!manager">
                <div>
                  <div class="d-flex">
                    <v-text-field
                      v-model="email"
                      label=""
                      @change="emailValidation"
                      :rules="email_rule"
                      :disabled="false"
                      required
                    />
                    <v-btn
                      text
                      large
                      outlined
                      style="font-size: 13px"
                      class="mt-3 ml-5"
                      color="teal lighten-1"
                      @click="checkDuplicateEmail"
                      :disabled="!emailPass"
                    >
                       <br /> 
                    </v-btn>
                  </div>

                  <div class="d-flex">
                    <v-text-field
                      v-model="password"
                      label=""
                      type="password"
                      :rules="password_rule"
                      :disabled="false"
                      required
                    />
                  </div>

                  <div class="d-flex">
                    <v-text-field
                      v-model="password_confirm"
                      label=" "
                      type="password"
                      :rules="password_confirm_rule"
                      :disabled="false"
                      required
                    />
                  </div>

                  <div class="d-flex">
                    <v-text-field
                      v-model="username"
                      label=""
                      :disabled="false"
                      required
                      color="black"
                    />
                  </div>

                  <div class="d-flex">
                    <v-text-field
                      v-model="birthdate"
                      label=" (8)"
                      :disabled="false"
                      :rules="birthdate_rule"
                      required
                      color="black"
                    />
                  </div>

                  <div class="d-flex">
                    <v-text-field
                      v-model="phoneNumber"
                      label=" ('-' 11)"
                      :disabled="false"
                      :rules="phoneNumber_rule"
                      required
                      color="black"
                    />
                  </div>
                  <div class="d-flex">
                    <v-text-field
                      v-model="city"
                      label=""
                      :disabled="true"
                      required
                    />
                  </div>

                  <div class="d-flex">
                    <v-text-field
                      v-model="street"
                      label=" "
                      :disabled="true"
                      required
                    />
                  </div>

                  <div class="d-flex">
                    <v-text-field
                      v-model="addressDetail"
                      label=" "
                      :disabled="false"
                      required
                    />
                  </div>

                  <div class="d-flex">
                    <v-text-field
                      v-model="zipcode"
                      label=""
                      :disabled="true"
                      required
                    />

                    <v-btn
                      text
                      large
                      outlined
                      style="font-size: 13px"
                      class="mt-3 ml-5"
                      color="blue lighten-1"
                      @click="callDaumAddressApi"
                      :disabled="false"
                    >
                       
                    </v-btn>
                  </div>

                  <v-btn
                    type="submit"
                    block
                    x-large
                    rounded
                    class="mt-6"
                    color="purple lighten-1"
                    :disabled="(emailPass & streetPass) == false"
                  >
                    
                  </v-btn>
                </div>
              </div>
              <div v-else>
                <div>
                  <div class="d-flex">
                    <v-text-field
                      class="mt-3"
                      v-model="managerCode"
                      label=" "
                      outlined
                      :disabled="false"
                      required
                      color="black"
                    />
                    <v-btn
                      text
                      large
                      style="font-size: 13px; height: 55px"
                      class="mt-3 ml-5 mr-0"
                      @click="checkManagerCode"
                      :disabled="false"
                    >
                       
                    </v-btn>
                  </div>
                  <div class="d-flex">
                    <v-text-field
                      v-model="email"
                      label=""
                      outlined
                      @change="emailValidation"
                      color="black"
                      hint="  2 "
                      :rules="email_rule"
                      :disabled="false"
                      required
                    />
                    <v-btn
                      text
                      large
                      style="font-size: 13px; height: 55px"
                      class="mt-0 ml-5 mr-0"
                      @click="checkDuplicateEmail"
                      :disabled="!emailPass"
                    >
                       
                    </v-btn>
                  </div>
                  <div class="d-flex">
                    <v-text-field
                      v-model="password"
                      label=""
                      type="password"
                      outlined
                      color="black"
                      :rules="password_rule"
                      :disabled="false"
                      required
                    />
                  </div>

                  <div class="d-flex">
                    <v-text-field
                      v-model="password_confirm"
                      label=" "
                      type="password"
                      outlined
                      color="black"
                      :rules="password_confirm_rule"
                      :disabled="false"
                      required
                    />
                  </div>

                  <div class="d-flex">
                    <v-text-field
                      v-model="username"
                      label=""
                      :disabled="false"
                      required
                      outlined
                      color="black"
                    />
                  </div>

                  <div class="d-flex">
                    <v-text-field
                      v-model="birthdate"
                      label=" (8)"
                      :disabled="false"
                      :rules="birthdate_rule"
                      required
                      outlined
                      color="black"
                    />
                  </div>

                  <div class="d-flex">
                    <v-text-field
                      v-model="phoneNumber"
                      label=" ('-' 11)"
                      :disabled="false"
                      :rules="phoneNumber_rule"
                      required
                      outlined
                      color="black"
                    />
                  </div>
                  <v-btn
                    type="submit"
                    block
                    x-large
                    style="height: 55px"
                    :disabled="(emailPass & managerPass) == false"
                  >
                    
                  </v-btn>
                </div>
              </div>
            </v-form>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import axios from 'axios'
import mainRequest from "@/api/mainRequest";


export default {
  name: 'SignUpForm',
  data () {
    return {
      manager: false,
      managerCode: '',

      email: '',
      password: '',
      password_confirm: '',
      phoneNumber: '',

      username: '',
      birthdate: '',

      city: '',
      street: '',
      addressDetail: '',
      zipcode: '',

      managerPass: false,
      emailPass: false,
      streetPass: false,

      email_rule: [
        (v) => !!v || ' .',
        (v) => {
          const replaceV = v.replace(/(\s*)/g, '')
          const pattern =
            /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/
          return pattern.test(replaceV) || '  .'
        }
      ],
      password_rule: [
        (v) =>
          this.state === 'ins'
            ? !!v || '  .'
            : true,
        (v) =>
          !(v && v.length >= 30) || ' 30    .'
      ],
      password_confirm_rule: [
        (v) =>
          this.state === 'ins'
            ? !!v || '  .'
            : true,
        (v) =>
          !(v && v.length >= 30) || ' 30    .',
        (v) => v === this.password || '  .'
      ],
      phoneNumber_rule: [
        (v) => !!v || '  .',
        (v) => {
          const replaceV = v.replace(/(\s*)/g, '')
          const pattern = /010-\d{4}-\d{4}/
          return (
            pattern.test(replaceV) ||
            ' 11 . ("-")'
          )
        }
      ],
      birthdate_rule: [
        (v) => !!v || ' 8 .',
        (v) => {
          const replaceV = v.replace(/(\s*)/g, '')
          const pattern =
            /^(19[0-9][0-9]|20\d{2})(0[0-9]|1[0-2])(0[1-9]|[1-2][0-9]|3[0-1])$/
          return pattern.test(replaceV) || '  .'
        }
      ]
    }
  },
  methods: {
    onSubmit () {
      if (this.$refs.form.validate()) {
        if (this.manager) {
          const authorityName = 'MANAGER'
          const managerCode = this.managerCode
          const managerCheck = this.manager
          const { email, password, username, birthdate, phoneNumber } = this
          this.$emit('submit', {
            email,
            password,
            username,
            birthdate,
            authorityName,
            managerCheck,
            managerCode,
            phoneNumber
          })
        } else {
          const authorityName = 'MEMBER'
          const managerCheck = this.manager
          const {
            email,
            password,
            username,
            birthdate,
            city,
            street,
            addressDetail,
            zipcode,
            phoneNumber
          } = this
          this.$emit('submit', {
            email,
            password,
            username,
            birthdate,
            authorityName,
            managerCheck,
            city,
            street,
            addressDetail,
            zipcode,
            phoneNumber
          })
        }
      } else {
        alert('  !')
      }
    },
    emailValidation () {
      const emailValid = this.email.match(
        /^(([^<>()[\]\\.,;:\s@]+(\.[^<>()[\]\\.,;:\s@]+)*)|(.+))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      )

      if (emailValid) {
        this.emailPass = true
      }
    },
    checkDuplicateEmail () {
      const emailValid = this.email.match(
        /^(([^<>()[\]\\.,;:\s@]+(\.[^<>()[\]\\.,;:\s@]+)*)|(.+))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      )

      if (emailValid) {
        const { email } = this
        mainRequest
          .post(`/member/check-email/${email}`)
          .then((res) => {
            if (res.data) {
              alert('  !')
              this.emailPass = true
            } else {
              alert(' !')
              this.emailPass = false
            }
          })
      }
    },
    checkManagerCode () {
      const { managerCode } = this
      mainRequest
        .post(`/member/check-manager/${managerCode}`)
        .then((res) => {
          if (res.data) {
            alert('  .')
            this.managerPass = true
          } else {
            alert('   .')
            this.managerPass = false
          }
        })
    },
    callDaumAddressApi () {
      new window.daum.Postcode({
        oncomplete: (data) => {
          let fullRoadAddr = data.roadAddress
          let extraRoadAddr = ''

          if (data.bname !== '' && /[||]$/g.test(data.bname)) {
            extraRoadAddr += data.bname
          }

          if (data.buildingName !== '' && data.apartment === 'Y') {
            extraRoadAddr +=
              extraRoadAddr !== ''
                ? ', ' + data.buildingName
                : data.buildingName
          }

          if (extraRoadAddr !== '') {
            extraRoadAddr = ' (' + extraRoadAddr + ')'
          }

          if (fullRoadAddr !== '') {
            fullRoadAddr += extraRoadAddr
          }

          this.city = data.sido
          this.zipcode = data.zonecode
          this.street = data.sigungu + ' ' + fullRoadAddr

          this.streetPass = true
        }
      }).open()
    }
  }
}
</script>

<style scoped></style>
